import { Component, OnInit, forwardRef, Input, Output, EventEmitter } from '@angular/core';
import { NG_VALUE_ACCESSOR, ControlValueAccessor, FormControl } from '@angular/forms';

@Component({
  // tslint:disable-next-line: component-selector
  selector: 'mbaas-search-list',
  templateUrl: './search-list.component.html',
  styleUrls: ['./search-list.component.css'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => SearchListComponent),
      multi: true
    }
  ]
})
export class SearchListComponent implements ControlValueAccessor {

  // tslint:disable-next-line: no-inferrable-types
  disabled: boolean = false;
  etiqueta: string;
  rex: string;
  lista: boolean;

  @Input() formControlItem: FormControl;
  @Input() customMessages: Array<{ type: string, message: string }> = [];
  // tslint:disable-next-line: no-input-rename variable-name
  @Input('value') _value: string;
  @Input() error: boolean;
  // tslint:disable-next-line: no-inferrable-types
  @Input() restric: string = '';
  // tslint:disable-next-line: no-inferrable-types
  @Input() placeHolder: string = 'Seleciona una opción';
  // tslint:disable-next-line: no-inferrable-types
  @Input() selectClass: string = '';
  @Input() datos: Array<any> = [];
  @Input() done: (item: any) => any = (item) => item.label;
  // tslint:disable-next-line: member-ordering no-inferrable-types
  @Input() limit: number = 10;
  // tslint:disable-next-line: no-output-native member-ordering
  @Output() change = new EventEmitter();
  // tslint:disable-next-line: no-output-native member-ordering
  @Output() focus = new EventEmitter();
  // tslint:disable-next-line: no-output-native member-ordering
  @Output() focusout = new EventEmitter();

  @Input() getLabel: (item) => string = (item) => item.label;
  @Input() getValue: (item) => string = (item) => item.value;
  onChange: any = () => { };
  onTouched: any = () => { };

  constructor() {
    this.rex = '';
    this.etiqueta = '';
    this.lista = false;
    this.change.emit('');
  }

  touch(): void {
    this.lista = true;
    this.onTouched();
  }

  ocutar(): void {
    this.lista = false;
  }

  setValue(item): void {
    this.etiqueta = this.getLabel(item);
    this.value = this.getValue(item);
  }

  get value() {
    return this._value;
  }

  set value(value) {
    this._value = value;
    this.onChange(value);
    this.onTouched();
    this.change.emit(value);
  }

  writeValue(value) {
    const resto = this.datos.filter(item => this.getValue(item) === value);
    if (resto.length !== 0) {
      this.etiqueta = this.getLabel(resto[0]);
    } else {
      this.etiqueta = '';
    }
    this._value = value;
    this.change.emit(resto.length !== 0 ? resto[0] : '');
  }

  registerOnTouched(fn) {
    this.onTouched = fn;
  }

  registerOnChange(fn) {
    this.onChange = fn;
  }

  setDisabledState?(isDisabled: boolean): void {
    this.disabled = isDisabled;
  }

  sendFocus(): void {
    this.focus.emit();
    this.rex = '';
  }

  sendFocusOut(): void {
    this.focusout.emit();
  }

  buscar(
    items: any[] = [],
    regx: any = '',
    done: (item: any) => any = this.done,
    limit: number = this.limit
  ): any {
    if (!items || !done) {
      return [];
    }
    let num = 0;
    const temp = items.filter(item => {
      if (num < limit && done(item).toLowerCase().indexOf(regx.toLowerCase()) !== -1) {
        num++;
        return true;
      }
      return false;
    });
    return !temp ? [] : temp;
  }

  filter(cadena: any): string {
    if (this.restric === '') {
      return cadena.target.value;
    }
    let out = '';
    for (let i = 0; i < cadena.target.value.length; i++) {
      if (this.restric.indexOf(cadena.target.value.charAt(i)) !== -1) {
        out += cadena.target.value.charAt(i);
      }
    }
    cadena.target.value = out;
    return out;
  }
}
