import { Component, OnInit, EventEmitter, Output, Input } from '@angular/core';

@Component({
  selector: 'mbaas-button-back-web',
  templateUrl: './button-back-web.component.html',
  styleUrls: ['./button-back-web.component.scss']
})
export class ButtonBackWebComponent implements OnInit {
  // tslint:disable-next-line: no-inferrable-types
  @Input() disableIf: boolean = false;
  // tslint:disable-next-line: no-inferrable-types
  @Input() label: string = '';
  // tslint:disable-next-line: no-inferrable-types
  @Input() buttonClass: string = 'button button--secondary button--mobileHidden';
  @Output() call = new EventEmitter();

  constructor() { }

  ngOnInit() {
  }

  click(): void {
    this.call.emit();
  }
}


