import { Component, OnInit, Input, ElementRef, ChangeDetectionStrategy, ChangeDetectorRef, AfterViewInit } from '@angular/core';
import { FormGroup, FormControl } from '@angular/forms';
import { INVALID_MESSAGES } from 'src/mbaas/mbaas.const';

@Component({
  selector: 'mbaas-invalid-messages',
  templateUrl: './invalid-messages.component.html',
  styleUrls: ['./invalid-messages.component.scss'],
  // changeDetection: ChangeDetectionStrategy.OnPush
})
export class InvalidMessagesComponent implements OnInit, AfterViewInit {
  @Input() formControlItem: FormControl;
  @Input() customMessages: Array<{ type: string, message: string }> = [];

  constructor(private cdr: ChangeDetectorRef) { }

  ngOnInit() {
    // console.log('formControlItem', this.formControlItem);
  }

  ngAfterViewInit() {
    // this.cdr.detectChanges();
  }

  public messages(validators: any): Array<string> {
    const messages = [];
    if (!validators) { return messages; }
    Object.keys(validators).forEach(key => {
      messages.push(this.message(key, validators));
    });
    return messages;
  }

  private message(key: string, validators: { [key: string]: any }): string {
    return this.customMessages.length > 0
    && this.customMessages.filter(i => i.type === key).length > 0
    ? this.customMessages.filter(i => i.type === key)[0].message
    : String(INVALID_MESSAGES[key]).replace('${value}', validators[key]);
  }

  get showErrors(): boolean {
    if (!this.formControlItem) { return false; }
    if (!this.formControlItem.errors) { return false; }
    return this.formControlItem.dirty || this.formControlItem.touched;
  }

}
