import { Component, forwardRef, Input, Output, EventEmitter, ViewChild, Injector, OnInit, Self, Optional } from '@angular/core';
import { NG_VALUE_ACCESSOR, ControlValueAccessor, FormControl, NgControl } from '@angular/forms';

@Component({
  // tslint:disable-next-line: component-selector
  selector: 'mbaas-swiss-input',
  templateUrl: './swiss-input.component.html',
  styleUrls: ['./swiss-input.component.css'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => SwissInputComponent),
      multi: true
    }
  ]
})
export class SwissInputComponent implements ControlValueAccessor {

  // ngControl: NgControl;
  constructor() {
    // if (this.ngControl) {
    //   this.ngControl.valueAccessor = this;
    // }
  }

  get value() {
    return this._value;
  }

  set value(value) {
    this._value = this.maxL({
      target: {
        value
      }
    });
    this.onChange(this._value);
    this.onTouched();
    this.change.emit(this._value);
  }

  @ViewChild('Mask', { static: false }) input;
  @Input() formControlItem: FormControl;
  @Input() customMessages: Array<{ type: string, message: string }> = [];
  mostrar: boolean;
  @Input() preffix: string;
  @Input() suffix: string;
  // tslint:disable-next-line: no-inferrable-types
  @Input() maxLength: number = Infinity;
  // tslint:disable-next-line: no-output-native member-ordering
  @Output() change = new EventEmitter();
  // tslint:disable-next-line: no-output-native member-ordering
  @Output() focus = new EventEmitter();
  // tslint:disable-next-line: no-output-native member-ordering
  @Output() focusout = new EventEmitter();
  // tslint:disable-next-line: no-output-native member-ordering
  @Output() showFormEdit: EventEmitter<any> = new EventEmitter();
  // tslint:disable-next-line: no-inferrable-types member-ordering
  @Input() inputClass: string = '';
  // tslint:disable-next-line: no-inferrable-types member-ordering
  @Input() placeHolder: string = '';
  // tslint:disable-next-line: no-inferrable-types member-ordering
  @Input() restric: string = '';
  // tslint:disable-next-line: member-ordering
  @Input() editPencil = false;
  // tslint:disable-next-line: member-ordering
  @Input() showInputsEdit = false;
  // tslint:disable-next-line: no-inferrable-types member-ordering
  @Input() disabled: boolean = false;
  // tslint:disable-next-line: no-inferrable-types member-ordering
  hiddenElement: boolean = false;
  // tslint:disable-next-line: no-input-rename variable-name member-ordering
  @Input('value') _value: string;
  // tslint:disable-next-line: member-ordering
  @Input() error: boolean;
  // tslint:disable-next-line: ban-types
  @Input() ngModelOptions: Object;
  @Input() maskValue: (value: string) => string = (value: string) => value;
  @Input() mapValue: (value: string) => string = (value: string) => value;
  onChange: any = () => { };
  onTouched: any = () => { };


  touch(): void {
    this.onTouched();
  }

  writeValue(value) {
    this._value = this.maxL({
      target: {
        value
      }
    });
    this.change.emit(this._value);
  }

  registerOnTouched(fn) {
    this.onTouched = fn;
  }

  registerOnChange(fn) {
    this.onChange = fn;
  }

  setDisabledState?(isDisabled: boolean): void {
    this.disabled = isDisabled;
  }

  sendFocus(): void {
    this.onTouched();
    this.focus.emit();
  }

  sendFocusOut(): void {
    this.focusout.emit();
  }

  maxL(element: any): string {
    if (element.target.value.length <= this.maxLength) {
      element.target.value = this.filter(this.mapValue(element.target.value.substring(0, element.target.value.length)));
      return element.target.value;
    }
    element.target.value = this.filter(this.mapValue(element.target.value.substring(0, this.maxLength)));
    return element.target.value;
  }

  mostrarInput(): void {
    this.mostrar = false;
    setTimeout(() => {
      this.input.nativeElement.focus();
    }, 100);
  }

  filter(cadena: string): string {
    let out = '';
    for (let i = 0; i < cadena.length; i++) {
      if (this.restric.indexOf(cadena.charAt(i)) !== -1) {
        out += cadena.charAt(i);
      }
    }
    return out;
  }

  focusMethod(): void {
    if (this.showInputsEdit) {
      this.hiddenElement = true;
      this.showFormEdit.emit(this.hiddenElement);
      return;
    }
    setTimeout(() => {
      this.input.nativeElement.focus();
    }, 100);
  }

  // Set touched on blur
  onBlur() {
      this.onTouched();
  }
}
